<template>
  <div class="maxset">
    <ul class="list-group">
      <li
        class="list-group-item m-1"
        v-for="item in getMenu"
        :key="item.id"
        @click="getId(item)"
        data-toggle="modal"
        data-target="#exampleModal"
      >
        <h2>{{ item.name }}</h2>
        <p>{{ item.mark }}</p>
        <div class="sizes">
          <span>中 {{ item.size.mid }}</span>
          <span v-if="item.size.big != ''">大 {{ item.size.big }}</span>
        </div>
      </li>
      <!-- Modal -->
      <li>
        <Modal ref="clear" :gettomodal="userclick"></Modal>
      </li>
    </ul>
  </div>
</template>

<script>
import Modal from "./modal.vue";

export default {
  name: "Limitedtime",
  data() {
    return {
      userclick: {
        //預設
        id: 1,
        name: "紅茶",
        mark: "測試",
        size: {
          mid: "10",
          big: "20",
        },
      },
    };
  },
  computed: {
    getMenu() {
      return this.$store.state.menudata.limitedtime;
    },
  },
  methods: {
    getId(item) {
      this.userclick = item;
      this.$refs.clear.clearchoose();
    },
  },
  components: {
    Modal,
  },
};
</script>

<style lang="scss" scoped>
.maxset {
  max-width: 800px;
  width: 100% !important;
  margin: auto;
  .list-group {
    max-height: 70vh;
    overflow-y: auto;
    -ms-overflow-style: none;
    &::-webkit-scrollbar {
      display: none;
    }
    .list-group-item {
      color: #eae6e6;
      background: rgb(87, 76, 87);
    }
  }
}

@media (max-width: "576px") {
  .list-group {
    max-height: 500px;
    h2 {
      font-size: 5vw;
    }
    p {
      font-size: 14px;
    }
  }
}
</style>


